import axios from "axios";
import yaml from "yaml";

import slokasFile from "../content/slokas/slokas.yaml";
import slokasMeaningFile from "../content/slokas/slokas-meaning.yaml";
import audioTimeFile from "../content/audio/audio-time.yaml";

async function fetchData(fileName) {
    const response = await axios(fileName);
    let responseObj = yaml.parse(response.data)
    return responseObj;
}

export let fetcher = {
    slokas: fetchData(slokasFile),
    audioTime: fetchData(audioTimeFile),
    meaning: fetchData(slokasMeaningFile)
}

// Assumed formats as below
// 10:20:30 hh:mm:ss
//    10:45    mm:ss
export function toSeconds(duration) {
    let format = duration.split(':');
    if (format.length === 3) {
        const [hours, minutes, seconds] = duration.split(':');
        return Number(hours) * 60 * 60 + Number(minutes) * 60 + Number(seconds);
    } else if (format.length === 2) {
        const [minutes, seconds] = duration.split(':');
        return Number(minutes) * 60 + Number(seconds);
    }
}

function toTime(seconds) {
    let mm = parseInt(seconds / 60)
    let ss = seconds - (mm * 60)
    return `${mm}:${ss}`
}

// example for 6th chapter: getStartTime(1, "00:23", 47, "10:06")
// output, for 6th chapter, use in the audio-time.yaml
function getStartTime(startSlokaNum, startTime, endSlokaNum, endTime) {
    let startSec = toSeconds(startTime)
    let endSec = toSeconds(endTime)
    let diffSec = endSec - startSec

    let diff = endSlokaNum - startSlokaNum
    let avgSec = diffSec / diff
    let arrCount = diff - 1

    return Array(arrCount).fill(avgSec)
        .map((num, i) => parseInt(startSec + num * (i + 1)))
        .map(num => toTime(num))
}

// Array(18).fill(12.4).map((num,i) => parseInt(3356+num *(i+1))).map(num => toTime(num))

// to know if given element is in ViewPort/screen
// refrence --- https://www.javascripttutorial.net/dom/css/check-if-an-element-is-visible-in-the-viewport/
export function isInViewport(element) {
    const rect = element.getBoundingClientRect();
    return (
        rect.top >= 0 &&
        rect.left >= 0 &&
        rect.bottom <= (window.innerHeight || document.documentElement.clientHeight) &&
        rect.right <= (window.innerWidth || document.documentElement.clientWidth)
    );
}

import audio1to5 from "../content/audio/Chapter-01-05.mp3";
import audio6to12 from "../content/audio/Chapter-06-12.mp3";
import audio13to18 from "../content/audio/Chapter-13-18.mp3";

export let getAudioUrl = chapter => {
    let audioFile;
    switch (true) {
        case [1, 2, 3, 4, 5].includes(chapter):
            audioFile = audio1to5;
            break;
        case [6, 7, 8, 9, 10, 11, 12].includes(chapter):
            audioFile = audio6to12;
            break;
        case [13, 14, 15, 16, 17, 18].includes(chapter):
            audioFile = audio13to18;
            break;
    }
    return audioFile;
}
