import {useReducer} from "react";
import {Routes, Route, HashRouter} from "react-router-dom";

import {Layout} from "./Layout";

import {TOC} from "../components/TOC";
import Stats from "../components/Stats";
import Book from "../components/Book";
import {BookContext, bookReducer, initialState} from "../components/BookReducer";


export default function BookRouter() {

    let [bookState, dispatch] = useReducer(bookReducer, initialState)

    return <HashRouter history={history}>
        <BookContext.Provider value={{bookState, dispatch}}>
            <Routes>
                <Route path="/" element={<Layout/>}>
                    <Route index element={<TOC/>}/>
                    <Route path="/stats" element={<Stats/>}/>
                    {
                        Array(18).fill(1)
                            .map((dummy, i) => i + 1)
                            .map(chapNum =>
                                <Route key={chapNum} path={`/${chapNum}`} element={<Book chapterNum={chapNum}/>}/>
                            )
                    }
                </Route>
                <Route path="*" element={<Layout/>}/>
            </Routes>
        </BookContext.Provider>
    </HashRouter>
}