import {createContext} from "react";

// ----------------------1. Reducer (state manager)----------------------

export function bookReducer(state, action) {
    switch (action.type) {

        // Active Sloka..
        case "active-sloka":
            state.activeSloka = action.data
            console.log(state)
            break

        // Audio monitor
        case "TimeUpdateListener":      // run listener while playing
            if (!state.timeupdateListener) {
                console.log("Reducer(case)TimeUpdateListener",)
                state.timeupdateListener = action.data
                // song.addEventListener('timeupdate', state.timeupdateListener);
            }
            break
        case "getSlokaFromCurrentTime": // get running sloka number -----------not working to look..
            if(!state.getSlokaFromCurrentTime) {
                state.getSlokaFromCurrentTime = action.data
            }
            break

        // Repeat Cases
        case "one":
            state.repeat.oneToggle = !state.repeat.oneToggle
            state.repeat.manyToggle = false
            break
        case "many":
            state.repeat.manyToggle = !state.repeat.manyToggle
            state.repeat.oneToggle = false
            break
        case "repeatFrom":
            state.repeat.fromSloka = action.data
            break
        case "repeatTo":
            state.repeat.toSloka = action.data
            break
        case "clear-repeat-many":
            state.activeSloka = 0
            if (state.repeat.manyToggle) {
                state.repeat.manyToggle = false
                state.repeat.fromSloka = 1
                state.repeat.toSloka = 5
            }
    }

    state = {...state}
    return state
}

export let initialState = {
    // 1. audio tag
    timeupdateListener: null,
    // 2. repeat button group
    repeat: {
        oneToggle: false,
        manyToggle: false,
        fromSloka: 1,
        toSloka: 5
    },
    // 3.
    activeSloka: 0
}

// ----------------------2. Context (descendent children to access)----------------------
export let BookContext = createContext()